.header{
    padding-top: 48px;
    
    &__container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-left: 70px;
        padding-right: 70px;
    }

    &__menu{
        display: flex;

        @include media ('<desktop') {
            display: none;
        }
    }

    &__list{
        display: flex;
        border-right: 1px solid black;
    }
    &__link{
        padding: 10px 30px;
        height: 100%;

        font-family: 'Mont';
        font-style: normal;
        font-weight: 800;
        font-size: 18px;
        line-height: 157%;
        color: #3C467A;
    }

    &__select{
        display: flex;
        align-items: center;
        font-family: 'Mont';
        font-style: normal;
        font-weight: 800;
        font-size: 18px;
        line-height: 157%;
        color: #3C467A;
        margin-left: 30px;
        border: none;
    }

    &__logo{
        fill: black;
    }
}

// Mobile menu btn

.menu-btn {
    display: none;

    @include media ('<desktop') {
        display: flex;
        margin-left: auto;
        background-color: transparent;
        border: none;

        padding: 10px 10px 10px 10px;

        cursor: pointer;
    }
   
}

